.navbar{
      background: linear-gradient(90deg, #55ada6 40%, rgb(26,23,23) 97%);
     /* background: linear-gradient(to bottom, #55ada6 94%,  #1a1818 100%); */
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
}

.navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: inherit;
    max-width: 1500px;
    width: 100%;
}

.navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 40px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
}

.logo{
    width: 21%;
    margin-bottom: 4%;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 125vh;
    justify-content: flex-end;
    margin-right: 2rem;
}

.nav-item {
    height:80px;
}

.nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
}

.nav-links:hover {
    border-bottom: 4px solid #e30052;
    transition: all 0.2s ease-out;
}
 a.nav-links:hover {
    color: #e30052;
    text-decoration: none;
}
.fa-bars {
    color: #fff;
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    .navbarItems {
        position: relative;
    }

    .nav-menu {
        justify-content: flex-start;
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 90vh;
        position: absolute;
        top:80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: #242424;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }

    .nav-links:hover {
        background-color: #fff;
        color: #242424;
        border-radius: 0;
    }

    .navbar-logo {
        /* position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%, 50%); */
        position: absolute;
        top: -31%;
        left: -17%;
        margin-left: 10%;
        transform: translate(25%, 50%);
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform:  translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .fa-times {
        color: #fff;
        font-size: 2rem;
    }

    .nav-links-mobile {
        display: block;
        text-align: center;
        margin: 2rem auto;
        border-radius:  4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: #fff;
        padding: 14px 20px;
        border: 1px solid #fff;
        transition: all 0.3s ease-out;
    }

    .nav-links-mobile:hover {
        background-color: #fff;
        color: #242424;
        transition: 250ms;
    }

}

@media screen and (max-width: 425px) {
    .navbar{
        width: 100%;
    }
    .navbar-logo {
        width: 75%;
    }
}